import React from 'react'
import PropTypes from 'prop-types'

export default function Navbar(props) {
    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
            <div className="container-fluid">
                <a className="navbar-brand" href="#">{props.title}</a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <a className="nav-link active" aria-current="page" href="#">Home</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">{props.about}</a>
                        </li>
                    </ul>
                    <div className={`form-check form-switch mx-4 text-${props.mode === 'light' ? 'dark' : 'light'}`}>
                        <input className="form-check-input" type="checkbox" onClick={props.toggleMode} id="flexSwitchCheckDefault" />
                        <label className="form-check-label " htmlFor="flexSwitchCheckDefault">{props.mode === 'light' ? 'Dark' : 'Light'}  mode</label>
                    </div>
                    <form className="d-flex" style={{ color: props.mode === 'dark' ? 'white' : 'black' }}>
                        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" style={{ backgroundColor: props.mode === 'light' ? 'white' : '#181818' }} />
                        <button className={`btn btn-${props.mode}`} type="submit">{props.searchButton}</button>
                    </form>
                </div>
            </div>
        </nav >
    )
}

Navbar.propTypes = {
    title: PropTypes.string.isRequired,
    about: PropTypes.string,
    searchButton: PropTypes.string.isRequired,
}

Navbar.defaultProps = {
    title: "Set title here",
    about: "About",
    searchButton: "Enter your search keyword"
}