import React, { useState } from 'react'
import PropTypes from 'prop-types'


export default function TextForm(props) {
    const handleLwClick = () => {
        // console.log("lowercase clicked.")
        let newText = text.toLowerCase();
        setText(newText);
        props.showAlert("Lower Case Conversion Done", "success");
    }
    const handleUpClick = () => {
        // console.log("uppercase clicked.")
        let newText = text.toUpperCase();
        setText(newText);
        props.showAlert("Upper Case Conversion Done", "success");
    }
    const handleOnChange = (event) => {
        // console.log("On change");
        setText(event.target.value);
    }
    const handleClearClick = () => {
        // console.log("uppercase clicked.")
        let newText = "";
        setText(newText);
        props.showAlert("Text in the textarea is cleared", "success");
    }
    const handleSpaceClick = () => {
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "));
        props.showAlert("Extra spaces in the text are removed", "success");
    }
    const handleCopyClick = () => {
        navigator.clipboard.writeText(text);
        props.showAlert("Copied to Clipboard !", "success");
    }

    const wordCount = (text) => {
        let newtext = text.split(" ");
        let count = 0;
        newtext.forEach(element => {
            if (element.length > 0)
                count++;
        });
        return count;
    }

    const [text, setText] = useState("Enter text here");

    return (
        <>
            <div className="container mb-3" style={{ color: props.mode === 'dark' ? 'white' : 'black' }}>
                <h2 className="my3" style={{ color: props.mode === 'light' ? 'black' : 'white' }}>{props.heading}</h2>
                <textarea className="form-control" value={text} onChange={handleOnChange}
                    style={{ backgroundColor: props.mode === 'light' ? 'white' : '#181818', color: props.mode === 'light' ? 'black' : 'white' }} rows="8" id="exampleFormControlTextarea1" ></textarea>
                <div className="container my-3" style={{ color: props.mode === 'light' ? 'black' : 'white' }}>
                    <button disabled={text.length === 0} className="btn btn-primary mx-2 my-1" onClick={handleUpClick}>Convert to UPPERCASE</button>
                    <button disabled={text.length === 0} className="btn btn-primary mx-2 my-1" onClick={handleLwClick}>Convert to lowercase</button>
                    <button disabled={text.length === 0} className="btn btn-primary mx-2 my-1" onClick={handleCopyClick}>Copy Text</button>
                    <button disabled={text.length === 0} className="btn btn-primary mx-2 my-1" onClick={handleSpaceClick}>Remove Extra Space</button>
                    <button disabled={text.length === 0} className="btn btn-primary mx-2 my-1" onClick={handleClearClick}>Clear Text</button>
                </div>
            </div>
            <div className="container mb-3" style={{ color: props.mode === 'dark' ? 'white' : 'black' }}>
                <h2 className=" my-3">Text Summary</h2>
                <p>Number of words : {wordCount(text)}.</p>
                <p>Number of characters : {text.length}.</p>
                <p>Minutes to read : {text.length === 0 ? 0 : 0.008 * text.split(" ").length} </p>
                <h2>Preview</h2>
                <p>{text.length === 0 ? "Nothing to preview" : text}</p>
            </div>
        </>
    )
}

TextForm.propTypes = {
    heading: PropTypes.string.isRequired,
}